.title {
    display: flex;
    justify-content: center;
    font-size: 24px;
    font-weight: bold;
    margin: 20px;
}

.list {
    flex-direction: column;
    align-items: center;
    margin: 10px;
    padding: 20px;
    max-height: 400px;
    background-color: white;
    z-index: 1;
    border-radius: 20px;
    box-shadow: 0 4px 16px var(--color-dark-grey);
}

.productName {
    font-size: 19px;
    font-weight: bold;
    margin-bottom: 10px;
}

.content {
    display: flex;
    flex-direction: column;
    font-size: 14px;
}

.text {
    display: flex;
    align-items: center;
    margin-top: 3px;
}

.role {
    background: var(--color-grey);
    padding: 5px;
    border-radius: 5px;
    margin-right: 5px;
}

p {
    margin: 0;
}